@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  scroll-behavior: smooth;
}



.app {
  min-height: 100vh;
  background-color: black;
  color: white;
  padding-top: 130px;
  padding-bottom: 70px;

}
@media(max-width:700px){
  .app{
    padding-top: 70px
  }
}
.pageTitle {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: "Montserrat", sans-serif;
  font-size: 2vw;
  padding: 4px;
  border-radius: 50px;
  color: white;
  padding-bottom: 2rem;
}
@media (max-width: 1000px ) {
  .pageTitle {
    font-size: 6.4vw;
  }
}
.Header {
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    background-color: #4C2882;
    font-family: "Montserrat", sans-serif;
    font-size: 5vw;
    padding-bottom: 15px;
    box-shadow: 0px 1px 5px black;
    color: white;
    z-index: 100;
    padding-top: 15px;
}

@media (max-width:1000px) {
    .header {
        padding-top: 15px;
        font-size: 6.4 vw;
    }
}
.media {
    display: flex;
    flex-direction: column;
    width: 200px;
    padding: 5px;
    margin: 5px 0;
    background-color: #282c34;
    border-radius: 10px;
    position: relative;
    font-family: "Montserrat", sans-serif;
}
.media:hover {
    background-color: white;
    color: black;
}
@media (max-width: 550px) {
    .media {
        width: 45%;
    }

}
.poster {
    border-radius: 10px;
}
.title {
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
}
.subTitle {
    display: flex;
    justify-content: space-between;
    padding-bottom: 3px;
    padding: 0 2px;
    padding-bottom:  3px;
}

.trending {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
