.Header {
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    background-color: #4C2882;
    font-family: "Montserrat", sans-serif;
    font-size: 5vw;
    padding-bottom: 15px;
    box-shadow: 0px 1px 5px black;
    color: white;
    z-index: 100;
    padding-top: 15px;
}

@media (max-width:1000px) {
    .header {
        padding-top: 15px;
        font-size: 6.4 vw;
    }
}